.iphone14 {
  width: 100%;
  height: 34em;
  overflow: hidden;
  box-sizing: border-box;
  position: relative;
}

.unit-background {
  background: url(iphone14__yellow.jpg) no-repeat center center;
  background-size: cover;
}

.unit-wrapper {
  display: flex;
  flex-direction: column;
}
.unit-link {
  position: absolute;
  display: flex;
  height: 100%;
}

.unit-text-wrapper {
  position: relative;
  text-align: center;
  height: 100%;
}

.iphone14-headline {
  margin-top: 1em;
  line-height: 1.07143;
  font-weight: 500;
  font-size: var(--headline-size);
  letter-spacing: -0.005em;
}

.iphone14-subhead {
  font-size: var(--subHeadline-size);
  font-weight: 300;
  line-height: 1.07143;
  letter-spacing: -0.005em;
  margin-top: 0.3em;
}

.callout {
  font-weight: 300;
  margin-top: 1em;
  color: #6e6e73;
  letter-spacing: 0.011em;
  line-height: 1.19048;
}

@media only screen and (max-width: 768px) {
  .iphone14 {
    height: 28em;
  }

  .iphone14-headline {
    font-size: 2.5em;
  }

  .iphone14-subhead {
    font-size: 1.5em;
  }

  .cta-links {
    font-size: 1em;
  }
}
